<template>
  <div class="g-fundetails">
    <div class="m-swiper">
      <cube-slide :showDots="showDots" :data="items"/>
    </div>
    <div class="m-content">
      <div class="s-title beyond">{{bit.proName}}</div>
      <div class="s-ps">
        <div class="number">目标金额：{{bit.fixIntegral}}积分</div>
        <div class="day">剩余{{time==0?'0天':time}}</div>
      </div>
      <div class="s-range">
        <div class="gird" :style="{'width':bit.percentage+'%'}"></div>
      </div>
      <div class="s-complete">
        <div class="now">当前进度：{{bit.percentage}}%</div>
        <div class="booster">{{bit.raiseNumber}}位支持者</div>
      </div>
    </div>
    <div class="m-setMeal">
      <div class="s-title">选择回报</div>
      <div class="s-navList">
        <div class="box" :class="{'active':acNumber==i}" v-for="(v,i) in activitys" :key="i" @click="acNumber=i">
          {{v.activityIntegral}}积分
        </div>
      </div>
      <div class="s-content" v-for="(v,i) in activitys" :key="i" v-show="acNumber==i">
        <div class="top">
          <div class="number">{{v.activityIntegral}}积分</div>
          <div>支持人数{{v.buyNumber}} 剩余{{v.activityNumber-v.buyNumber}}份</div>
        </div>
        <div class="center after">
          <div class="img">
            <img :src="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+v.picMain" alt="">
          </div>
          <div class="title">{{v.title}}</div>
        </div>
        <div class="bottom">
          {{v.intro}}
        </div>
        <div class="other">
          <div class="give">配送方式：免运费</div>
          <div class="day">预计回报时间：{{v.repayDay}}天</div>
        </div>
      </div>
      <div class="s-details">
        <div class="title">众筹故事</div>
        <div class="img">
          <img v-for="(v,i) in bit.pics" :key="i" :src="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+v" alt="">
        </div>
      </div>
    </div>
    <buyfixed :member="shop.username" :proName="activitys[acNumber].title" :price="activitys[acNumber].activityIntegral"
              :sortId="bit.sortId" :activityId="activitys[acNumber].id" :pic="activitys[acNumber].picMain"/>
  </div>
</template>

<script>
  import buyfixed from '@/components/buyfixed'

  export default {
    name: "fundetails",
    data() {
      return {
        showDots: false,
        acNumber: 0,//套餐显示
        items: [],//轮播图
        bit: {},//基础信息
        shop: {},//店铺信息
        time:0,//倒计时
        activitys: [{intro: '', activityIntegral: '', id: ''}]
      }
    },
    created() {
      this.details();//商品详情
    },
    methods: {
      details() {
        this.$post(this.$bee + '/api/product/productDetail', {id: this.$route.query.id}).then((data) => {
          if (data.code == 200) {
            this.settime(data.data.pro.deadline);//日期倒计时
            this.shop = data.data.shop;
            this.bit = data.data.pro;
            this.activitys = data.data.activitys
            data.data.pro.carousels.forEach((i) => {
              this.items.push({
                url: "javascript:void(0)",
                image: 'https://fx18.oss-cn-hangzhou.aliyuncs.com/' + i
              })
            })
          } else {
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
      settime(v){//日期倒计时
        if(!v){return ;}
        const reg = new RegExp( '-' , "g" )
        v=v.replace(reg ,'/');
        let timer=setInterval(()=>{
          let now = new Date();
          let endDate = new Date(v);
          let times = endDate.getTime() - now.getTime();
          if (times >= 0) {
            let d = Math.floor(times / 1000 / 60 / 60 / 24);
            let h = Math.floor(times / 1000 / 60 / 60 % 24);
            let m = Math.floor(times / 1000 / 60 % 60);
            let s = Math.floor(times / 1000 % 60);
            this.time=d +'天'+ h+'小时'+ m+'分'+s+'秒'
          }else{
            this.time=0;
            clearInterval(timer);
          }
        },1000)
      }
    },
    components: {
      buyfixed
    }
  }
</script>

<style scoped lang="stylus">
  .g-fundetails
    padding-bottom 50px

  .m-swiper
    width 375px
    & >>> .cube-slide
      width 100%
      img
        width 100%
        height 100%

  .m-content
    padding 7px 15px 5px
    border-bottom 3px solid #e9e9e9;
    .s-title
      font-size 15px
      color: #4b4b4b
      text-align left
    .s-ps
      display flex
      justify-content: space-between
      margin-top 10px
      div
        color: #585858;
        font-size 11px
        &.day
          font-size 12px
    .s-range
      width 350px
      height 12px
      margin-top 5px
      background-color: #cdcdcd;
      box-shadow: inset 0px 1px 3px 0px rgba(0, 0, 0, 0.35);
      border-radius: 4px;
      position relative
      .gird
        position absolute
        top 0
        bottom 0
        left 0
        background-color: #81d8f5;
        border-radius: 4px;
    .s-complete
      display flex
      justify-content: space-between
      margin-top 10px
      div
        font-size 9px
        color: #81d8f5;
        &.booster
          font-size 11px
          color: #6b6b6b;

  .m-setMeal
    padding-top 17px
    .s-title
      color: #585858;
      font-size 13px
      text-align left
      text-indent 10px
    .s-navList
      padding 15px 15px 0 15px
      display flex
      flex-wrap wrap
      .box
        margin 0 15px 15px
        width: 82px;
        height: 30px;
        line-height 30px
        border-radius: 3px;
        border: solid 1px #c8c8c8;
        color: #cccccc;
        font-size 13px
        box-sizing border-box
        &.active
          border-color #db3838
          color: #de4e4e;
    .s-content
      width: 323px;
      height auto
      background-color: #f5f5f5;
      border-radius: 3px;
      margin 0 auto
      padding 15px
      box-sizing border-box
      .top
        display flex
        justify-content: space-between;
        padding-bottom 11px
        border-bottom 1px solid #cbcbcb;
        div
          color: #bcbcbc;
          font-size 11px
          line-height 16px
        .number
          color: #474747;
          font-size 16px
      .center
        padding-top 10px
        .img
          float left
          width: 100px;
          height: 67px;
          background-color: #db3838;
          img
            width 100%
            height 100%
        .title
          float left
          width calc(100% - 115px)
          height auto
          font-size: 16px;
          line-height 1.2
          margin-left 10px
          margin-top 15px
          color: #585858;
          text-align left
          font-weight bold
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
      .bottom
        color: #828282;
        font-size 12px
        margin-top 10px
        line-height 1.4
        text-align left
      .other
        margin-top 15px
        color: #828282;
        font-size 12px
        text-align left
        .day
          margin-top 5px
    .s-details
      .title
        margin-top 30px
        margin-bottom 15px
        color: #db3838;
        font-size 12px
      .img
        width 100%
        img
          width: 100%
</style>
