<template>
  <div class="g-buyfixed">
    <div class="m-fixed">
      <router-link :to="{name:'index'}" class="s-home" tag="div" >
        <img src="../assets/images/sy2.png" alt="">
        <span>首页</span>
      </router-link>
      <div class="s-serve" @click="finding">联系客服</div>
      <div class="s-buy" @click="showState">立即购买</div>
    </div>
    <transition name="slide-fade">
      <div class="dialog-box" v-show="showdia">
        <div class="m-choose">
          <div class="top">
            <div class="s-img">
              <img :src="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+pic" alt="">
            </div>
            <div class="s-msg">
              <div class="name beyond">{{proName}}</div>
              <span v-if="sortId==1||sortId==2">积分：{{price}}</span>
              <span v-else>元宝：{{price}}</span>
            </div>
          </div>
          <div class="center after">
            <div class="s-number">数量</div>
            <div class="s-adjustment">
              <span class="sub" @click="count('sub')"></span>
              <input type="number" v-model="number" pattern="[0-9]*" @blur="reduceCartNum">
              <span class="add" @click="count('add')"></span>
            </div>
          </div>
          <div @click="orderGo" class="bottom s-btn">确认</div>
          <i class="s-close" @click="showState"></i>
        </div>
      </div>
    </transition>
    <allbk v-show="showdia" />
  </div>
</template>

<script>
  import allbk from '@/components/allBk'

  export default {
    name: "buyfixed",
    props:['member','price','proName','sortId','activityId',"pic"],
    data(){
      return{
        showdia:false,
        number:1,
      }
    },
    methods:{
      showState(){
        this.showdia=!this.showdia
      },
      count(str){
        if(str=='add'){
          this.number++;
        }else if(str=='sub'){
          this.number>1&&this.number--;
        }
      },
      reduceCartNum(){
        if(this.number<=0){
          this.number=1;
          this.$createToast({
            txt: '不能小于0哦',
            type: 'warn'
          }).show()
        }
      },
      finding(){
        var _this=this;
        this.$createDialog({
          type: 'alert',
          title: '蜂信号：'+ _this.member
        }).show()
      },
      orderGo(){
        this.$store.commit("ORDERSAVE",{number:this.number,proId:this.$route.query.id,activityId:this.activityId});
        this.$router.push({name:'orderConfirm'});
      }
    },
    components: {
      allbk
    }
  }
</script>

<style scoped lang="stylus">
  .m-fixed
    position fixed
    bottom 0
    width 100%
    background #fff
    display flex
    flex-wrap wrap
    border-top #dedede
    font-size 12px
    z-idnex 100000
    &>div
      display block
    .s-home
      color #ffae00
      width 75px
      text-align center
      padding-top 5px
      img
        width 19px
        height 19px
        margin-bottom 3px
      span
        color #ffae00
        display block
    .s-serve
      font-family SimHei
      width 150px
      line-height 44px
      background #dbab38
      color #fff
      font-size 23px
    .s-buy
      font-family SimHei
      width 150px
      line-height 44px
      background #db3838
      color #fff
      font-size 23px
  .dialog-box
    position fixed
    left 0
    right 0
    bottom 0
    height 250px
    z-index 100000001
    .m-choose
      padding 14px
      box-sizing border-box
      position absolute
      bottom 0
      width 100%
      height 250px
      background #fff
      .top
        display flex
        flex-wrap wrap
      .s-img
        width 60px
        height 60px
        img
          width 100%
          height 100%
      .s-msg
        width calc(100% - 75px)
        margin-left 15px
        text-align left
        padding-top 10px
        padding-bottom 10px
        .name
          color: #444444;
          font-size 13px
          padding 5px 0
        span
          display block
          margin-top 7px
          font-size 15px
          color: #cc5b24;
      .center
        margin-top 30px
        position relative
      .s-number
        vertical-align middle
        color: #bdbdbd;
        font-size: 13px;
        text-align left
      .s-adjustment
        vertical-align middle
        text-align right
        position absolute
        right 0
        top -7px
        *
          vertical-align middle
        span
          display inline-block
          width 10px
          height 10px
          background url("../assets/images/sub.png") no-repeat center center
          background-size 100%
          &.sub
            background-image url(../assets/images/sub.png)
          &.add
            background-image url(../assets/images/add.png)
        input
          background-color: #e0e0e0;
          width: 30px;
          height: 20px;
          text-align center
          color: #ffffff;
          font-size 12px
          margin 0 5px
      .s-btn
        width: 178px;
        height: 28px;
        line-height 28px
        background-color: #e36324;
        border-radius: 3px;
        font-size: 16px;
        color #fff
        margin 80px auto 0
      .s-close
        display block
        width 12px
        height 12px
        background url(../assets/images/close.png) no-repeat center center
        position absolute
        top 10px
        right 10px
  .slide-fade-enter-active, .slide-fade-leave-active
    transition: all .5s
  .slide-fade-enter, .slide-fade-leave-to
    transform: translateY(100px);
    opacity: 0;
</style>
